import React, { useEffect, useState, useContext } from "react";
import { ProductsContext } from "../../shared/provider/ProductsProvider";

export const Product = ({ name, price, count, description, index }) => {
  const [products, setProducts] = useContext(ProductsContext);
  const [inCart, setInCart] = useState();

  useEffect(() => {
    if (!count) setInCart(0);
    else setInCart(count);
  }, []);

  useEffect(() => {
    console.log(products[index]);

    let updateProd = products[index];
    updateProd.count = inCart;

    console.log(updateProd);

    setProducts([
      ...products.slice(0, index),
      updateProd,
      ...products.slice(index + 1),
    ]);
  }, [inCart]);

  return (
    <div>
      <h1>{name}</h1>
      <p>Price: {price} kr</p>
      <p>{description}</p>
      <span>In cart: </span>

      <button onClick={() => setInCart(inCart + 1)}>+</button>

      <span>{inCart}</span>
      <button
        onClick={() => {
          if (inCart > 0) setInCart(inCart - 1);
        }}
      >
        -
      </button>
    </div>
  );
};
