import React, {useState} from 'react'
import './GreetUser.css'

export const GreetUser = ({name}) => {
    const [count, setCount] = useState(0)
    const increaseCount = () => {
        setCount(count + 1)
    }

    const showAlert = () => {
        alert("You've clicked me!")
    }
    return (
        <div className="greetuser">
            <h1 className="greetuser__header" onClick = {() => showAlert()}>Welcome to my website {name}!</h1>
            <h3 className="greetuser__subheader" onClick = {() => increaseCount()}>You've clicked me {count} times!</h3>
        </div>
    )
}
