import React, { useContext, useEffect } from "react";
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";
import { HomeView } from "../view/homeview/HomeView";
import { SignInView } from "../view/signinview/SignInView";
import { StoreView } from "../view/storeview/StoreView";
import { UserContext } from "../shared/provider/UserProvider";
import RoutingPath from "./RoutingPath";
import LocalStorage from "../shared/storage/LocalStorage";
import { ProfileView } from "../view/authenticatedviews/profileview/ProfileView";
import { SettingsView } from "../view/authenticatedviews/settingsview/SettingsView";
import { AccessoriesView } from "../view/accessoriesview/AccessoriesView";

export const Routes = ({ children }) => {
  const [authenticatedUser, setAuthenticatedUser] = useContext(UserContext);

  const isUserAuthenticated = () => {
    const userFromBrowserMemory = localStorage.getItem(LocalStorage.username);
    setAuthenticatedUser(userFromBrowserMemory);
  };

  useEffect(() => {
    isUserAuthenticated();
  }, []);

  const blockIfAuthenticated = (view) => {
    return authenticatedUser ? HomeView : view;
  };

  const blockIfNotAuthenticated = (view) =>
    authenticatedUser ? view : SignInView;

  return (
    <BrowserRouter basename="/WebbramverkreactWeek3">
      {children}
      <Switch>
        <Route
          exact
          path={RoutingPath.signInView}
          component={blockIfAuthenticated(SignInView)}
        />
        <Route exact path={RoutingPath.homeView} component={HomeView} />
        <Route exact path={RoutingPath.storeView} component={StoreView} />
        <Route
          exact
          path={RoutingPath.accessoriesView}
          component={AccessoriesView}
        />
        <Route
          exact
          path={RoutingPath.profileView}
          component={blockIfNotAuthenticated(ProfileView)}
        />
        <Route
          exact
          path={RoutingPath.settingsView}
          component={blockIfNotAuthenticated(SettingsView)}
        />
        <Redirect exact from="/" to={RoutingPath.homeView} />
      </Switch>
    </BrowserRouter>
  );
};
