import Axios from "axios";
import { useState } from "react";

export const AccessoriesView = () => {
  const [serverResponse, setServerResponse] = useState();
  const [counter, setCounter] = useState(1);

  const fetchData = async () => {
    const API_URL = `https://pokeapi.co/api/v2/pokemon/${counter}`;
    try {
      const response = await Axios.get(API_URL);
      setServerResponse(response);
      setCounter(counter + 1);
    } catch (error) {
      alert("Error retrieving desired data from server: " + error);
    }
  };

  return (
    <div>
      <h1>This is the accessories view</h1>
      <h2>{serverResponse?.data?.name}</h2>

      <button onClick={() => fetchData()}>Make API call</button>
      <br />
      {serverResponse?.data && (
        <img src={serverResponse?.data?.sprites?.front_default} />
      )}
      {serverResponse?.data && (
        <img
          src={serverResponse?.data?.sprites?.other?.dream_world?.front_default}
        />
      )}
    </div>
  );
};
