import React from "react";
import { Link, NavLink, useHistory } from "react-router-dom";
import Logo from "../../../shared/img/logo.svg";
import "./DesktopNavigation.css";
import RoutingPath from "../../../routes/RoutingPath";
import { useState, useContext } from "react";
import { UserContext } from "../../../shared/provider/UserProvider";
import { Profile } from "../../profile/Profile";

export const DesktopNavigation = () => {
  const data = "Björn";
  const data2 = "Bergqvist";
  const history = useHistory();
  const [authenticatedUser, setAuthenticatedUser] = useContext(UserContext);

  const renderLogin = () => {
    if (authenticatedUser)
      return (
        <div className="navbar__login--desktop">
          <Profile />
        </div>
      );
    return (
      <NavLink to={RoutingPath.signInView} activeClassName="selected">
        Sign in
      </NavLink>
    );
  };

  return (
    <nav className={"navbar"}>
      <img
        className={"navimg"}
        src={Logo}
        onClick={() => history.push("/home", data)}
      />
      <ul className={"navlist"}>
        <li>
          <NavLink
            to={{ pathname: "/home", state: data2 }}
            activeClassName="selected"
          >
            Home
          </NavLink>
        </li>
        <li>
          <NavLink to="/about" activeClassName="selected">
            About
          </NavLink>
        </li>
        <li>
          <NavLink to={RoutingPath.accessoriesView} activeClassName="selected">
            Accessories
          </NavLink>
        </li>
        <li>
          <NavLink to="/store" activeClassName="selected">
            Store
          </NavLink>
        </li>
        <li>{renderLogin()}</li>
      </ul>
    </nav>
  );
};
